import { useStore } from "effector-react";
import p5 from "p5";
import React, { useEffect } from "react";
import styled from "styled-components";

import { ids$ } from "../store/ids";
import { iterationIndex$, setIterationIndex } from "../store/iterationIndex";
import { fetchIterations, iterations$ } from "../store/iterations";
import { size$ } from "../store/size";
import { fromIndex } from "../utils";

const TILE_SIZE = 12;
// const TILE_SIZE = 24;

const saveIteration = async(iteration, diff) => {
  const payload = {
    _id: iteration._id,
    title: iteration.title,
    data: iteration.data,
    expectedRank: iteration.expectedRank,
    timestamp: iteration.timestamp,
    ...diff,
  };

  console.log("saveIteration", payload);

  await fetch("https://heartrate.miran248.now.sh/api/iteration", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(payload),
  });

  fetchIterations();
};

const sketch = (iteration, size) => (p) => {
  const ids = ids$.getState();

  const W = size.x * TILE_SIZE;
  const H = ((size.y + 1) * size.z - 1) * TILE_SIZE;

  p.setup = () => {
    p.createCanvas(W, H, p.CANVAS);
    p.noLoop();
    p.textSize(TILE_SIZE * .6);
    p.textAlign(p.CENTER, p.CENTER);
  };
  p.draw = () => {
    // aligns to top left
    // p.translate(-W / 2, -H / 2, 0);

    for(let i in iteration) {
      const { x, y, z } = fromIndex(size, i);

      const posX = x * TILE_SIZE;
      const posY = y * TILE_SIZE;
      const posZ = z * (size.y + 1) * TILE_SIZE;

      let c = 255;

      if(iteration[i] === 2)
        c = 51;
      else if(iteration[i] === 1)
        c = 204;

      p.fill(c);
      p.rect(posX, posY + posZ, TILE_SIZE, TILE_SIZE);

      p.fill(255 - c);
      // p.text(i, posX + TILE_SIZE * .5, posY + posZ + TILE_SIZE * .5);
      if(ids[i] > 0)
        p.text(ids[i], posX + TILE_SIZE * .5, posY + posZ + TILE_SIZE * .5);
    }
  };
};

const Cards = styled.div`
  display: grid;
  gap: 1ch;
  grid-auto-flow: column;
  overflow-x: auto;
`;

const Card = ({ id, index, iteration }) => {
  const selectedIndex = useStore(iterationIndex$);
  const size = useStore(size$);

  useEffect(() => {
    const p = new p5(sketch(iteration.combined, size), id);

    return p.remove;
  }, []);

  return (
    <button id={id}
            onClick={() => setIterationIndex(index)}
            disabled={true || selectedIndex === index}>
      <div>
        <label><input type="checkbox" defaultChecked={iteration.valid} disabled={!("_id" in iteration)} onClick={() => saveIteration(iteration, { valid: !iteration.valid })} /> valid</label>
      </div>
      <div>{iteration.title}</div>
      <div>dog {Math.round(iteration.dog)}bpm, maja {Math.round(iteration.maja)}bpm, delta {Math.round(Math.abs(iteration.maja - iteration.dog))}bpm</div>
      <div>{"_id" in iteration ? `expected ${iteration.expectedRank}, ` : ""}actual {iteration.actualRank}</div>
      <div>trainable {iteration.trainable.toString()} ({iteration.output})</div>
    </button>
  );
};

export default () => {
  const iterations = useStore(iterations$);

  if(iterations === null) {
    return (
      <div>loading iterations...</div>
    );
  }

  return (
    <div>
      <h6>total iterations</h6>
      <p>{iterations.length}</p>
      <Cards>
      {iterations.map((iteration, i) => {
        const id = `pick-container-${iteration.title}`;

        return (
          <Card key={id}
                id={id}
                index={i}
                iteration={iteration} />
        );
      })}
      </Cards>
    </div>
  );
};
